round-size: cover;\n  display: flex;\n  height: 100vh;\n  justify-content: center;\n  width: 100vw;\n}\n\n.box-layout__box {\n  background: fade-out(white, .15);\n  border-radius: 3px;\n  padding: $l-size $m-size;\n  text-align: center;\n  width: 25rem;\n}\n\n.box-layout__title {\n  margin: 0 0 $m-size 0;\n  line-height: 1;\n}\n",".button {\n  background: $blue;\n  border: none;\n  color: white;\n  display: inline-block;\n  font-size: $font-size-large;\n  font-weight: 300;\n  line-height: 1;\n  padding: $s-size;\n  text-decoration: none;\n}\n\n.button--link {\n  background: none;\n}\n\n.button--secondary {\n  background: #888;\n}\n",".header {\n  background: $dark-blue;\n}\n\n.header__content {\n  align-items: center;\n  display: flex;\n  justify-content: space-between;\n  padding: $s-size 0;\n}\n\n.header__title {\n  color: white;\n  text-decoration: none;\n  h1 {\n    margin: 0;\n  }\n}\n",".content-container {\n  margin: 0 auto;\n  padding: 0 $m-size;\n  max-width: 80rem;\n}\n",".page-header {\n  background: $off-white;\n  margin-bottom: $l-size;\n  padding: $l-size 0;\n}\n\n.page-header__actions {\n  margin-top: $m-size;\n}\n\n.page-header__title {\n  font-weight: 300;\n  margin: 0;\n  span {\n    font-weight: 700;\n  }\n}\n",".input-group {\n  display: flex;\n  flex-direction: column;\n  margin-bottom: $m-size;\n  @media (min-width: $desktop-breakpoint) {\n    flex-direction: row;\n    margin-bottom: $l-size;\n  }\n}\n\n.input-group__item {\n  margin-bottom: $s-size;\n  @media (min-width: $desktop-breakpoint) {\n    margin: 0 $s-size 0 0;\n  }\n}\n",".text-input {\n  border: 1px solid #cacccd;\n  height: 50px;\n  font-size: $font-size-large;\n  font-weight: 300;\n  padding: $s-size;\n}\n\n.select {\n  @extend .text-input;\n}\n\n.textarea {\n  @extend .text-input;\n  height: 10rem;\n}\n",".form {\n  display: flex;\n  flex-direction: column;\n  >* {\n    margin-bottom: $m-size;\n  }\n}\n\n.form__error {\n  margin: 0 0 $m-size 0;\n  font-style: italic;\n}\n",".show-for-mobile {\n  @media (min-width: $desktop-breakpoint) {\n    display: none;\n  }\n}\n\n.show-for-desktop {\n  @media (max-width: $desktop-breakpoint - .01rem) {\n    display: none;\n  }\n}\n",".list-header {\n  background: $off-white;\n  border: 1px solid darken($off-white, 7%);\n  color: $grey;\n  display: flex;\n  justify-content: space-between;\n  padding: $s-size $m-size;\n}\n\n.list-body {\n  margin-bottom: $m-size;\n  @media (min-width: $desktop-breakpoint) {\n    margin-bottom: $l-size;\n  }\n}\n\n.list-item {\n  border: 1px solid darken($off-white, 7%);\n  border-top: none;\n  color: $dark-grey;\n  display: flex;\n  flex-direction: column;\n  padding: $s-size;\n  text-decoration: none;\n  transition: background .3s ease;\n  &:hover {\n    background: $off-white;\n  }\n  @media (min-width: $desktop-breakpoint) {\n    align-items: center;\n    flex-direction: row;\n    justify-content: space-between;\n    padding: $m-size;\n  }\n}\n\n.list-item--message {\n  align-items: center;\n  color: $grey;\n  justify-content: center;\n  padding: $m-size;\n  &:hover {\n    background: none;\n  }\n}\n\n.list-item__title {\n  margin: 0;\n  word-break: break-all;\n}\n\n.list-item__sub-title {\n  color: $grey;\n  font-size: $font-size-small;\n}\n\n.list-item__data {\n  margin: $s-size 0 0 0;\n  @media (min-width: $desktop-breakpoint) {\n    margin: 0;\n    padding-left: $s-size;\n  }\n}\n",".loader {\n  align-items: center;\n  display: flex;\n  height: 100vh;\n  justify-content: center;\n  width: 100vw;\n}\n\n.loader__image {\n  height: 6rem;\n  width: 6rem;\n}\n"],"sourceRoot":""}]);
